<template>
  <div class="paymentMethodOption">
    <PMethodHeader :description="description" :title="paymentOption.title" :links="links" />
    <div class="_action">
      <Options v-if="paymentOption.sub_type == 'IOptions'" class="_sandboxRadioButton" :optionsName="'payplug_' + paymentOption.name" :optionsItems="paymentOption.options" />
      <Switch v-if="paymentOption.sub_type == 'switch'" :switchName="paymentOption.name" :switchChecked="paymentOption.checked ? true : null" />
    </div>
  </div>
</template>

<script>
import Switch from "../../atoms/AtomSwitch";
import PMethodHeader from "@/components/organisms/PMethod/OrganismPMethodHeader";
import Options from "@/components/atoms/AtomOptions";

export default {
  name: "OrganismPMethodOption",
  components:{Options, PMethodHeader, Switch },
  props: ["environment", "paymentOption"],
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.paymentOption.descriptions[this.environment].description ?? null;
      this.links = this.paymentOption.descriptions[this.environment].link_know_more ?? null;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }
}
</script>

