<template>
  <div class="payplugConfiguration">
    <div class="payplug">
      <br/><br/>
      <Header :environment="environment" :headerImage="logo" :headerTitle="plugin_title" :headerDescription="plugin_description" :headerSmallText="plugin_version" :headerOptions="options"/>
      <Login :environment="environment" v-if="!logged && !register" v-on:notRegisted="updateparent" v-on:showLoggedIn="showLoggedIn" />
      <Subscribe :environment="environment" v-if="!logged && register" v-on:notRegisted="updateparent" />
      <LoggedIn :environment="environment" v-if="logged" v-on:showLogin="showLogin" v-on:modeChanged="modeChanged" />
      <PMethods :environment="environment" v-bind:class="{ '-disabled' : !logged}" />
      <Status :environment="environment" v-bind:class="{ '-disabled' : !logged}" />
    </div>
  </div>
</template>

<script>
import Header from "@/components/organisms/OrganismSectionHeader";
import Login from "@/components/organisms/OrganismSectionLogin";
import Subscribe from "@/components/organisms/OrganismSectionSubscribe";
import LoggedIn from "@/components/organisms/OrganismSectionLoggedIn";
import Status from "@/components/organisms/OrganismSectionStatus";
import PMethods from "@/components/organisms/OrganismSectionPMethods";

export default {
  name: "app",
  components:{ Header, Login, Subscribe, LoggedIn, Status, PMethods },
  data(){
    return {
      environment: process.env.VUE_APP_ENV,
      logged: this.jsonData.settings.logged,
      register: false,
      plugin_version: this.jsonData.header.text_woocommerce_plugin_version,
      plugin_title: this.jsonData.header.text_plugin_title,
      plugin_description: this.jsonData.header.text_plugin_description,
      options: this.jsonData.header.options_plugin_visible ?? this.jsonData.header.options_plugin_enable,
      logo: "svg/logo-payplug.svg"
    }
  },
  methods: {
    updateparent(status) {
      this.register = status
    },
    showLoggedIn() {
      this.logged = true
    },
    showLogin() {
      this.logged = false
    },
    modeChanged(value) {
      this.environment = (value === 0) ? "live" : "sandbox"
    },
  }
}
</script>

