<template>
  <div class="payplugUISwitch" :class="(switchDisabled) ? '-disabled': ''">
    <label class="_switch">
      <input type="checkbox" @change="emitChange" :disabled="(switchDisabled) ? 'disabled' : null" :checked="(switchChecked) ? 'checked' : null" :name="switchName">
      <span class="_slider"></span>
      <span v-if="switchEnabledLabel" class="_label"
            :data-enable-text="(switchEnabledLabel) ? switchEnabledLabel : null"
            :data-disable-text="(switchDisabledLabel) ? switchDisabledLabel : null"></span>
    </label>
  </div>
</template>

<script>

export default {
  name: "AtomSwitch",
  props: {
    switchName: {
      type: String,
      required: false
    },
    switchDisabled: {
      type: String,
      required: false
    },
    switchChecked: {
      type: String,
      required: false
    },
    switchEnabledLabel: {
      type: String,
      required: false
    },
    switchDisabledLabel: {
      type: String,
      required: false
    },
  },
  methods: {
    emitChange(e) {
      this.$emit('changed', e.target.checked)
    }
  }
}
</script>

