<template>
  <select class="payplugUISelect" :name="selectOptions.name"
          :disabled="(selectDisabled) ? 'disabled' : null">
    <option v-for="option in selectOptions"
            v-bind:key="option"
            :value="option.value"
            :selected="(selectSelected == option?.value) ? 'selected' : null"
            :disabled="(option?.disabled) ? 'disabled' : null">
      {{ option.label }}
    </option>
  </select>
</template>

<script>
  export default {
    name: "AtomSelect",
    props:{
      selectOptions: {
        type: Object,
        required: true
      },
      selectSelected: {
        type: String,
        required: false
      },
      selectDisabled: {
        type: Boolean,
        required: false
      }
    }
  }
</script>


