<template>
  <input
      :type="(inputType) ? inputType : null"
      :value="(inputValue) ? inputValue : null"
      :step="(inputType=='number' && inputStep) ? inputStep : null"
      :min="(inputType=='number' && inputMin) ? inputMin : null"
      :max="(inputType=='number' && inputMax) ? inputMax : null"
      :disabled="(inputDisabled) ? 'disabled' : null"
      :placeholder="(inputPlaceholder) ? inputPlaceholder : null"
  />
</template>

<script>
export default {
  name: "AtomInput",
  props: {
    inputType: {
      type: String,
      required: true,
      default: "text"
    },
    inputDisabled: {
      type: String,
      required: false
    },
    inputValue: {
      type: String,
      required: false
    },
    inputStep: {
      type: String,
      required: false
    },
    inputMin: {
      type: String,
      required: false
    },
    inputMax: {
      type: String,
      required: false
    },
    inputPlaceholder: {
      type: String,
      required: false
    }
  }
}
</script>

